@import url(../css/global.css);

.header {
  background-color: var(--secondary-color);
  width: 100%;
  margin-bottom: 30px;
}

.header .header-container {
  display: flex;
  padding: 10px 0;
  align-items: center;
}

.header .header-row {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.header .logo {
  color: white;
  font-size: 20px;
  cursor: pointer;
  transition: 0.3s;
}

.header .logo:hover {
  color: var(--primary-color);
  font-size: 20px;
}

.header .search-btn {
  width: 100%;
  background-color: white;
  color: var(--primary-color);
  height: 100%;
  border: 1px solid var(--primary-color);
  border-radius: 10px;
  transition: 0.3s;
}

.header .search-btn:hover {
  background-color: var(--primary-color);
  color: white;
}

@media (max-width: 767px) {
  .header .header-container {
    justify-content: center;
  }
  .header .logo {
    display: flex;
    justify-content: center;
    padding: 5px 0;
  }
  .header .search-btn {
    margin: 5px 0;
    padding: 5px 0;
  }
  .header .header-row {
    flex-direction: column;
    align-items: center;
  }
}
