@import url(../css/global.css);

.title-cont {
  margin-bottom: 30px;
}

.title-col {
  display: flex;
  justify-content: center;
}

.title {
  font-size: 30px;
  position: relative;
}

.title::after {
  position: absolute;
  content: "";
  width: 150%;
  height: 3px;
  background-color: var(--primary-color);
  bottom: -10px;
  left: -25%;
}
