@import url(../css/global.css);

.category-btn {
  border: 2px solid var(--primary-color);
  width: fit-content;
  padding: 5px;
  font-weight: 600;
  margin: 7px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.2s;
  display: inline-block;
}

.category-btn:hover {
  background-color: var(--primary-color);
  color: white;
}
