.meal {
  box-shadow: 5px 5px 6px #e7e7e7;
  padding: 0;
}

.meal-card {
  width: 100%;
  border: 1px solid #e7e7e7;
  margin: 10px 0 5px;
  border-radius: 10px;
}

.meal-img {
  max-width: 100%;
  padding: 0;
}

.meal-img img {
  border-radius: 10px;
}

.meal-info {
  padding: 10px;
}

.meal-title,
.meal-price {
  width: fit-content;
  font-size: 20px;
}

.meal-title-price {
  display: flex;
  justify-content: space-between;
}

.meal-desc {
  padding: 10px 12px;
}
